import FieldComponent from './FieldComponent';
import InputField from "./InputField";

class DateOfBirth extends FieldComponent {

    static defaultProps = {
        id: 'dateOfBirth',
        Component: InputField,
        label: {
            id: 'dateOfBirth', defaultMessage: 'Date of birth'
        },
        editProps: {
        }
    };

    getProps () {
        const id = this.props.id;
        const sect = this.getParent();
        const value = sect.getValue(id,'');
        const touched = sect.isTouched(id);

        if (touched && !parseInt(value,10)) {
            return { maxLength: 5, type: 'text', style: { backgroundColor: '#f9dfe2', color: 'red' } };
        }
        return { maxLength: 5, type: 'text' };
    }

    validate () {
        console.log( 'validate', this.props );
        return [];
    }
}

export default DateOfBirth;



