import React, { Component } from 'react';
import './CurrencyPicker.css';

export default class CurrencyPicker extends Component {

    onChange = (ev) => {
        this.setState( { value: ev.target.value } );
        // document.getElementById( this.props.id ).focus();
    };

    render () {
        const { id } = this.props;
        const state = this.state || {};
        const value = state.value || 'CNY';

        return (
            <div className="currencyPickDiv">
                <select
                    id={id+'-Pick'}
                    onBlur={this.props.onBlur}
                    onChange={this.onChange}
                    value={value}
                    className="currencyPick"
                >
                    <option disabled value="">Please select a currency &nbsp;</option>
                    <option value="CNY">CNY Chinese Yuan </option>
                    <option value="HKD">HKD Hong Kong Dollars &nbsp;</option>
                    <option value="USD">USD American Dollars </option>
                </select>

                <span aria-hidden="true" className="currencyLozenge">{value}</span>

                <svg viewBox="0 0 24 26" xmlns="http://www.w3.org/2000/svg" focusable="false" tabIndex="-1" aria-hidden="true">
                    <path stroke-width="5" d="M 2 12 L 12 23 L 22 12" />
                </svg>

                <div className="currencyInputDiv">
                    <input id={id} className="currencyInput" type="tel"/>
                </div>
            </div>
        );
    }
}



