import React, { Component } from 'react';

class ToolTip extends Component {

	constructor (props) {
		super(props);
		this.state = { showTip: false };
	}

	componentDidUpdate () {
		if (this.state.showTip && this.boxRef) {
			this.boxRef.focus();
		}
	}

	onClick = () => {
		this.setState( { showTip: !this.state.showTip } );
	};

	onFocus = () => {
		clearTimeout(this.timeoutHide);
	};

	onBlurHideTip = () => {
		this.timeoutHide = setTimeout(()=>{
			this.setState( { showTip: false } );
		},50);
	};

	focusOnButton = () => {
		this.buttonRef.focus();
		this.setState( { showTip: false } );
	};

	onKeyDown = (e) => {
		console.log('onKeyDown',e.keyCode);
		switch(e.keyCode) {
			case 8:
			case 13:
			case 27:
			case 32:
			case 46:
				this.focusOnButton();
				break;
			default:
		}
	};

	render () {
		const { children, label } = this.props;
		let boxClass = 'tip-box';
		if (this.state.showTip && this.buttonRef) {
			const r = this.buttonRef.getBoundingClientRect();
			console.log('TipButton',r);
			if (r.x<120) {
				boxClass += ' tip-left';
			} else {
				const w = document.body.clientWidth;
				if (r.x>w-140) boxClass += ' tip-right';
			}
		}
		return (
			<span aria-label={label} className="tooltip">
				<button
					aria-label={this.props['aria-label']}
					className="tip-button"
					onClick={this.onClick} 
					onFocus={this.onFocus}
					ref={(el)=>{this.buttonRef = el;}}
					type="button"
				>
					<span aria-hidden="true">?</span>
				</button>
				{this.state.showTip && 
					<span
						aria-live="assertive"
						className={boxClass}
						onBlur={this.onBlurHideTip} 
						onClick={this.focusOnButton} 
						onKeyDown={this.onKeyDown}
						ref={(el)=>{this.boxRef = el;}}
						tabIndex="0"
					>
						{children}
					</span>
				}
			</span>
		);
	}
}

export default ToolTip;

/*
click button while tip is open
want to hide tip
get onBlur then later onClick
*/



