import React, { Component } from 'react';
import './App.css';

class Popup extends Component {

	componentDidMount () {
		//console.log('POPUP OPENED',this.props.onCancel);
		document.getElementById('thePopup').focus();
	}

	/*onCancel = () => {
		if (this.props.onCancel) this.props.onCancel();
	};*/

	clickPopup = (event) => {
		event.stopPropagation();
	};

	render () {
		const props = this.props;
		return (
      		<div className="popup-outer" onClick={props.onCancel}>
      			<div id="thePopup" className="popup-inner" role="dialog" tabIndex="0" onClick={this.clickPopup}>
      				{props.children}
      			</div>
      		</div>
		);
	}
}

export default Popup;

/*
very simple popup implementation

<Popup onCancel={this.closePopup}>
	<h3>This is a popup</h3>
	<p>Go to next page</p>
	<p>
        <Button onClick={this.closePopup} className='white'>Cancel</Button>
        {' '}
        <Button onClick={this.gotoNext}>Continue</Button>
	</p>
</Popup>

*/



