import React from 'react';
import Button from '../Button';
import FormBasis from './FormBasis';

import PersonTitle from './PersonTitle';
import FormSection from './FormSection';
import PhoneSection from './PhoneSection';
import GenericInput from './GenericInput';

const Resource = {};
const FooBar = Resource.FooBar;

export default class TestSimpleForm extends FormBasis {

    onClickReset = () => {
        this.setFormData( {}, {} );
    };

    onClickTest = () => {
        console.log('TEST',this.state);
    };

    render () {
        return (
            <div className="form-fields">
                <p>A simple form with id {this.props.id} at {new Date().toISOString()}</p>
                <PersonTitle/>

                <p><b>Guarantor</b></p>
                <FormSection {...this.sectionProps('guarantor')}>
                    <PersonTitle/>
                    <GenericInput id="fullName" label={{ defaultMessage: 'Full name' }} editProps={{ placeholder: 'Full name' }} />
                    <p><b>AKA</b></p>
                    <FormSection {...this.sectionProps(['guarantor','aka'])}>
                        <PersonTitle/>
                        <GenericInput id="fullName" editProps={{ placeholder: 'Full name' }} />
                    </FormSection>
                </FormSection>

                <PhoneSection {...this.sectionProps('contact')}/>
                {FooBar && <FooBar id={'haha'}/>}

                <div className="button-bar">
                    <Button id="idCancel" onClick={this.onClickCancel} className='white' aria-label="Click here to cancel this form">Cancel</Button>
                    <Button id="idReset" onClick={this.onClickReset} className='white'>Reset</Button>
                    <Button id="idTest" onClick={this.onClickTest} className='primary'>Continue</Button>
                </div>
            </div>
        );
    }

}

/*
<FormSection
    id="guarantor"
    values={this.getValue('guarantor',{})}
    touched={this.getFieldProps('guarantor',{})}
>
 */



