import React, { Component } from 'react';
import PropTypes from 'prop-types';
// import fetch from "whatwg-fetch";
import logo from './logo.svg';
import './App.css';
import Button from './Button';
import DateInput from './DateInput';
import SelectInput from './SelectInput';
import CheckBox from './CheckBox';
import ToolTip from './ToolTip';
import InfoGrid, { optionFormatter, percentFormatter, dateFormatter } from './InfoGrid';
import Popup from './Popup';
import Announcer, { announceText } from './Announcer';

import DateOfBirth from './client/DateOfBirth';
import PersonTitle from './client/PersonTitle';
import CurrencyPicker from './client/CurrencyPicker';
import TestSimpleForm from './client/TestSimpleForm';
import TestRepeatForm from './client/TestRepeatForm';

let tsTouchError;

class App extends Component {

    static childContextTypes = {
        getParentSection: PropTypes.func
    };

  constructor (props) {
    super(props);
    this.state = { formValues: {}, touched: {}, showPop: false };
  }

  getChildContext () {
      return {
          getParentSection: () => { return this }
      };
  }

  componentDidMount () {
    announceText('Welcome to this React accessibility test page');
    window.scrollTo(0,0);
    this.focus('theHeader');
    window.onpopstate = () => {
      console.log('POPSTATE!');
      this.setState( { _drawt: new Date() } );
    };
  }

  focus (id) {
    const elem = document.getElementById(id);
    if (elem) elem.focus();
  }

  // CLICK HANDLERS

  onClickButton = (id) => () => {
    alert('You clicked the button '+id);
    this.focus(id);
    const things = this.render();
    console.log(things);
  };

  onClickCancel = () => {
    // setTimeout( () => announceText('You clicked the cancel button'), 1000 );
    announceText('You clicked the cancel button');
    // this.focus('genderType');
    this.focus('foreName');
  };

  onClickReset = () => {
    console.log('ONRESET');
    clearTimeout(tsTouchError);
    announceText('Reset was done');
    this.focus('idReset');
    this.setState( { formValues: {}, touched: {} } );
  };

  onClickContinue = () => {
    announceText(navigator.userAgent);
    // fetch('/api/food')
    const data = { msg: 'Hello '+new Date().toISOString() };
    fetch('/api/debug', { method: 'POST', body: JSON.stringify(data), headers: { 'content-type': 'application/json; charset=utf-8' } } )
      .then( (result) => {
        console.log('FETCH',result);
        // return result.blob();
        return result.text();
      })
      .then( (body) => {
        console.log('BODY',body);
      });
  };

  doRefresh = () => {
    this.setState( { showPop: false } );
    window.scrollTo(0,0);
    window.location.reload(true);
  };

  cbFocus = () => {
    document.getElementById('confirmMail').focus();
  };

  changeURL = (path) => () => {
    window.history.pushState( {}, null, path );
    this.setState( { _drawt: new Date() } );
    window.scrollTo(0,0);
  };

  // popup

  onClickShowPop = () => {
    this.setState( { showPop: true } );
  };

  closePopup = () => {
    this.setState( { showPop: false } );
    announceText('Popup was closed');
  };

  // change input

  cancelTouchError () {
    clearTimeout(tsTouchError);
  }

  onTouchField (id,dat) {
      const tc = this.state.touched;
      tc[id] = dat;
      this.setState( { touched: tc } );
  }

  onBlur = (ev) => {
    console.log('ONBLUR',ev.target.id);
    const id = ev.target.id;
    tsTouchError = setTimeout( () => {
      const tc = this.state.touched;
      tc[id] = true;
      this.setState( { touched: tc } );
    }, 100 );
  };

  onEdit = (ev) => {
    // console.log('onEdit',ev.target.id,ev.target.value);
    const fv = this.state.formValues;
    fv[ev.target.id] = ev.target.value;
    this.setState( { formValues: fv } );
  };

  getData () {
      return this.state.formValues;
  }

  getSectionId () {
    return 'theForm';
  }

  getValue (id, def = '') {
    return this.state.formValues[id] || def;
  }

  isTouched (id) {
  	return this.state.touched[id];
  }

  // rendering

  renderButton ( id, text, ariaLabel, onclick ) {
    return (
      <button id={id} aria-label={ariaLabel} onClick={onclick} tabIndex="0" type="button">{text}</button>
    );
  }

  renderError (id,error) {
    // role="alert"
    return (<div className="inline-error">
        <span className="error-icon">●</span>{' '}
        <span id={id+'-Error'} aria-live="assertive">{error}</span>
      </div>);
  }

  renderInput ( id, labelText, more, error ) {
    const invalid = (this.state.touched[id] && !!error) || undefined;
    const describedBy = invalid ? id+'-Error' : undefined;
    return (
      <div>
        <label htmlFor={id} className={ invalid ? 'invlabel' : undefined }>{labelText}</label>
        <input id={id} type="text" {...more}
          onBlur={this.onBlur}
          onChange={this.onEdit}
          value={this.getValue(id)}
          aria-invalid={invalid}
          aria-describedby={describedBy}
          className={ invalid ? 'inval' : undefined }
        />
        {invalid && this.renderError(id,error)}
      </div>
    );
  }

  renderSelect ( id, labelText, options, more, error ) {
    const invalid = (this.state.touched[id] && !!error) || undefined;
    const describedBy = invalid ? id+'-Error' : undefined;
    const value = this.getValue(id);
    const gray = value==='' ? 'gray' : undefined;
    
    const opts = options.reduce( (acc,val) => {
      if (val.id==='') {
        if (value==='') // do not add the '' option if a selection has been made
          acc.push( <option key="" value="" className="gray" disabled>{val.text}</option> );
      } else
        acc.push( <option key={val.id} value={val.id} disabled={val.disabled}>{val.text}</option> );
      return acc;
    }, []);
    
    // aria-label={'AL '+labelText} // works on Safari
    return (
      <div>
        <label htmlFor={id} className={ invalid ? 'invlabel' : undefined }>{labelText}</label>
        <select id={id} {...more}
          onBlur={this.onBlur}
          onChange={this.onEdit}
          value={value}
          aria-invalid={invalid}
          aria-describedby={describedBy}
          className={ invalid ? 'inval' : undefined || gray }
        >{opts}</select>
        {invalid && this.renderError(id,error)}
      </div>
    );
  }
  
  isNumeric (val) {
    return val==='' || new RegExp('^-?([0-9]+[,.]?)*[.,]?[0-9]+$').test(val);
  }

  renderPopup () {
    return (
      <Popup onCancel={this.closePopup}>
        <h3>This is a popup</h3>
        <p>Go to next page</p>
        <CheckBox form={this} id="isWalrus">I am a walrus</CheckBox>
        <p className="button-bar">
          <Button onClick={this.closePopup} className='white'>Cancel</Button>
          <Button onClick={this.doRefresh}>Continue</Button>
        </p>
      </Popup>
    );
  }

    onRepFormDebug = (state) => {
        console.log('IT WORKED!',state);
    };

  render() {
      const loca = window.location;
      // console.log(loca);
      if (loca.pathname==='/new') {
          return <div><p>New test form</p><TestSimpleForm id={'newTestSimple'}/></div>;
      }
      if (loca.pathname==='/search') {
          return <div><TestRepeatForm id={'testRepForm'} onDebug={this.onRepFormDebug}/></div>;
      }

	  const showPop = this.state.showPop;
    //const props = { style: { backgroundColor:'#f0f0f0', color: '#008000', padding: '8px' } };
    //    <p><span {...props}>This is an alert!</span></p>

    const surname = this.getValue('surName');
    const surNameError = surname ? (surname.indexOf('$')<0 ? null : 'Illegal surname!' ) : 'Enter a surname';
    const foreNameError = this.getValue('foreName') ? null : 'Enter a forename';
    const genderError = this.getValue('genderType') ? null : 'Please select a gender';

    const productNo = this.getValue('productNo');
    const productType = this.getValue('productType');
    const prodError = this.isNumeric(productNo) ? null : 'Please enter numeric chars only';
    const prodTypeError = productType==='C' && !productNo ? 'Computers must have a product code' : null;

    const blur = showPop ? { style: { filter: 'blur(2px)' } } : {};

    const tip = <ToolTip aria-label="Help for delivery date">When would you like this delivered?</ToolTip>;

    return (
      <div className="App">
        <Announcer/>
        <header id="theHeader" className="App-header" tabIndex="0">
          <span className="logo-span">
            <img src={logo} className="App-logo" alt="logo" role="presentation" aria-hidden="true" style={{ position: 'absolute' }}/>
          </span>
          <h1 className="App-title">React Accessibility</h1>
        </header>
        <p className="App-intro">
          This is an accessibility test app.
        </p>
        
        <form className="form-fields" {...blur}>
        {this.renderSelect('genderType','Your Gender',genders,undefined,genderError)}
        {this.renderInput('surName','Last Name',undefined,surNameError)}
        {this.renderInput('foreName','First Name',undefined,foreNameError)}

        <PersonTitle id="otherTitle" label={{ id:'i89', defaultMessage: 'Other title' }} />
        <PersonTitle />
        <DateOfBirth />

            <label htmlFor="pickSalary">Salary</label>
            <CurrencyPicker id="pickSalary"/>
            <label htmlFor="pickIncome">Other income</label>
            <CurrencyPicker id="pickIncome"/>

        <div className="button-bar">
          <Button id="idCancel" onClick={this.onClickCancel} className='white' aria-label="Click here to cancel this form">Cancel</Button>
          <Button id="idReset" onClick={this.onClickReset} className='white'>Reset</Button>
          {this.renderButton('idContinue','Continue','Press continue to proceed',this.onClickContinue)}
        </div>

        <h3 className="brand">Product Details</h3>
        <p>This product has various features which will be described here. You should be able to read this text.
          <ToolTip>Here is a tooltip with some help about this thing.</ToolTip>
        </p>

        {this.renderInput('productNo','Product Code', { 'aria-label': 'Enter Code or Reference' }, prodError )}
        {this.renderSelect('productType','Product Category', productTypes, { 'aria-label': 'Select type of product' }, prodTypeError )}
        <SelectInput form={this} id="YAYAY" labelText="Or another" options={productTypes} more={{ 'aria-label': 'Alternative product' }}
          hidden={[productType]}
        />
        <DateInput form={this} id="DOB" labelText="Delivery Date" tooltip={tip}/>

        <CheckBox form={this} id="confirmMail">Please add me to your mailing list.</CheckBox>
        <CheckBox form={this} id="checkLarge">This is some very long text here as you can see. This is some very long text here as you can see. This is some very long text here as you can see. </CheckBox>

        <div className="button-bar">
            <Button onClick={this.cbFocus} className='white'>Focus</Button>
            <Button id="idShowPop" onClick={this.onClickShowPop} className='white'>Popup</Button>
            {this.renderButton('idExit','Exit',{ className: 'white' },this.doRefresh)}
            <Button id="wtf" onClick={this.onClickButton('wtf')}>WTF</Button>
        </div>

        <h3 className="brand">More options</h3>
        <p>Example information grid</p>
        <InfoGrid definitions={definitions} values={mockData} />
        <p>This page is {loca.pathname}, select an option below</p>
        <div className="button-bar">
          <Button onClick={this.changeURL('/products')} className="white">Product List</Button>
          <Button onClick={this.changeURL('/search')} className="white">Search</Button>
          <Button onClick={this.changeURL('/new')} className="white">New Application</Button>
        </div>
        <p>&nbsp;</p>
        <p>&nbsp;</p>
        </form>
        {showPop && this.renderPopup()}
      </div>
    );
  }
}

export default App;

/*
 <p {...props}>Cool! Suckers!</p>
*/

const genders = [
  { id: '', text: ''}, // Please select
  { id: 'M', text: 'Male'},
  { id: 'F', text: 'Female'},
  { id: 'Q', text: 'Other'}
];

const productTypes = [
  { id: '', text: 'Please select a category'}, // Please select
  { id: 'C', text: 'Computer'},
  { id: 'T', text: 'Television'},
  { id: 'B', text: 'Biscuits'},
  { id: 'Q', text: 'Other'},
  { id: 'X', text: 'Garbage', disabled: true }
];

function makeMap (arr) {
  const res = {};
  arr.forEach( (el) => {
    res[el.id] = el.text;
  });
  return res;
}

const genderMap = makeMap(genders);
const productTypesMap = makeMap(productTypes);

const prodTip =
	<ToolTip aria-label="Product type help">
		This is the info about the type of product the customer selected.
		<br aria-hidden="true"/>
		Information can span several lines
	</ToolTip>;

const definitions = [
  { name: 'Gender', label: 'Gender', mapper: genderMap, format: optionFormatter },
  { name: 'ProdType', label: 'Type of Product', mapper: productTypesMap, format: optionFormatter, tooltip: prodTip },
  { name: 'Surname' },
  { name: 'DOB', label: 'Date of Birth', format: dateFormatter },
  { name: 'Rate', label: 'Discount rate', format: percentFormatter }
];

const mockData = {
  Gender: 'F',
  ProdType: 'T',
  Surname: 'Jones',
  Rate: 2.9876,
  DOB: { day: '07', month: '04', year: '1985' }
};

