import React from 'react';
import FormSection from './FormSection';
import FormRepeatingSection from './FormRepeatingSection';
import GenericInput from './GenericInput';

export default class PhoneSection extends FormSection {

/*    constructor () {
        super();
        this.state = { fading: false };
    } */

    deletePhone = () => {
        //const el = document.getElementById(this.getFullId());
        //el.style.backgroundColor = '#d8d8d8';
        this.setState( { fading: true } );
        setTimeout(() => {
            this.setState( { fading: false } );
            this.getParent().deleteSectionIndex( this.props.id );
        },250);
    };

    arraymove (arr, fromIndex, toIndex) {
        const element = arr[fromIndex];
        arr.splice(fromIndex, 1);
        arr.splice(toIndex, 0, element);
    }

    moveIndex = (index,direction) => {
        const sect = this.getParent();
        const values = [...sect.props.values];
        const touched = [...sect.props.touched];
        this.arraymove(values,index,index+direction);
        this.arraymove(touched,index,index+direction);
        sect.getParent().onFieldChange(sect.props.id, values, touched);
    };

    pulse () {
        this.setState( { pulse: true } );
        setTimeout(() => {
            this.setState( { pulse: false } );
        },500);
    }

    moveUp = () => {
        this.pulse();
        setTimeout(()=>{
            this.moveIndex(this.props.id,-1);
        },200);
    };

    moveDown = () => {
        this.pulse();
        setTimeout(()=>{
            this.moveIndex(this.props.id,1);
        },100);
    };

    getFullId () {
        return this.getParent().getSectionId()+'-'+this.props.id;
    }

    static getDefaultValues () {
        return {
            homeNo: '+89',
            __specialKey: Date.now()
        };
    }

    renderRepeatButtons () {
        const cc = this.getParent().getSectionCount();
        const id = this.props.id;
        return (
            <p style={{ textAlign: 'right' }}>
                {cc>1 && id>0 && <span><a onClick={this.moveUp}>Move Up</a> | </span>}
                {cc>1 && id<cc-1 && <span><a onClick={this.moveDown}>Move Down</a> | </span>}
                <a onClick={this.deletePhone}>Delete Phone</a>
            </p>
        );
    }

    render () {
        const sect = this.getParent();
        const isrep = sect instanceof FormRepeatingSection;
        if (isrep) {
            // const cc = sect.getSectionCount();
            // console.log('PhoneSection',isrep,cc);
        }
        const fade = {};
        if (this.state) {
            if (this.state.fading) {
                // fade.backgroundColor = '#f0cdb3';
                fade.opacity = 0.5;
            } else if (this.state.pulse) {
                fade.outlineColor = '#f0cdb3';
                fade.outlineWidth = '3px';
                fade.outlineStyle = 'solid';
            }
        }

        return (
            <div id={this.getFullId()} style={fade}>
                <p>Phone section {this.props.id}</p>
                <GenericInput id="homeNo" editProps={{ maxLength: 12, placeholder: 'home phone' }} />
                <GenericInput id="mobileNo" editProps={{ placeholder: 'mobile phone' }}/>
                <GenericInput id="workNo" editProps={{ maxLength: 32, placeholder: 'office or work phone' }} />
                {isrep && this.renderRepeatButtons()}
            </div>
        );
    }

}



