import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class FormSection extends Component {

    static propTypes = {
        id: PropTypes.oneOfType( [ PropTypes.string, PropTypes.number ] ),
        values: PropTypes.shape({}),
        touched: PropTypes.shape({})
    };

    static contextTypes = {
        getParentSection: PropTypes.func
    };

    static childContextTypes = {
        getParentSection: PropTypes.func
    };

    getChildContext() {
        return {
            getParentSection: () => {
                return this
            }
        };
    }

    getParent () {
        return this.context.getParentSection();
    }

    getSectionId () {
        return this.getParent().getSectionId() + '-' + this.props.id;
    }

    getValue (id,def) {
        return this.props.values[id] || def;
    }

    isTouched (id) {
        return !!this.props.touched[id];
    }

    onTouchField (id,dat) {
        const touched = { ...this.props.touched };
        touched[id] = dat;
        this.getParent().onTouchField(id,touched)
    }

    onFieldChange (id, value, touched) {
        const modValues = {...this.props.values};
        const modTouched = {...this.props.touched};
        modValues[id] = value;
        modTouched[id] = touched;
        this.getParent().onFieldChange(this.props.id, modValues, modTouched);
    }

    onBlur (ev) {
        //const vals = { ...this.props.touched };
        //vals[ev.target.id] = ev.target.value;
        //const mod = { target: { id: this.props.id, value: vals } };
        //this.getParent().onEdit(mod);
    }

    onEdit (ev) {
        const vals = { ...this.props.values };
        vals[ev.target.id] = ev.target.value;
        const mod = { target: { id: this.props.id, value: vals } };
        this.getParent().onEdit(mod);
    }

    render () {
        return <div>{this.props.children}</div>;
    }

}



