import React, { Component } from 'react';

class CheckBox extends Component {

	/*onChange = (ev) => {
		console.log('CB',ev.target.checked);
		ev.target.value = ev.target.checked ? 'Y' : '';
		this.props.form.onEdit(ev);
	};*/

    onClick = (ev) => {
        const { form, id } = this.props;
        console.log('CB');
        const checked = form.getValue(id)!=='Y';
        ev.target.value = checked ? 'Y' : '';
        this.props.form.onEdit(ev);
        ev.preventDefault();
        ev.stopPropagation();
        return false;
    };

	render () {
        const { form, id, children } = this.props;
        const checked = form.getValue(id)==='Y';
        return (
            <div style={{ padding: '16px 0 18px', position: 'relative' }}>
                <button className={checked ? 'radio-check' : 'radio'} id={id} onClick={this.onClick} type="button"
                    style={{ position: 'absolute', top: '15px' }}
                >
					<span>{' '}</span>
				</button>
                <label htmlFor={id} id={id+'-Label'} style={{ marginLeft: '34px', display: 'inline-block', width: 'auto', padding: '0' }}>
                    {children}
                </label>
            </div>
        );
	}

	/*zrender () {
		const { form, id, children } = this.props;
	    const checked = form.getValue(id)==='Y';
		return (
			<div>
				<input id={id} type="checkbox" onChange={this.onChange} checked={checked}/>
				<label htmlFor={id} tabIndex="0">
					{children}
				</label>
			</div>
		);
	}*/
}

export default CheckBox;



