import React, { Component } from 'react';
import './App.css';

class Button extends Component {

	render () {
		const props = this.props;
		return (
      		<button {...props} tabIndex="0" type="button">{this.props.children}</button>
		);
	}
}

export default Button;

export const stateTree = {
	personalDetails: {
		forename: { value: 'F', touched: true },
		surname: {},
		dob: { value: { day: '07', month: '11', year: '1992' } },
		consentEmail: { value: 'N' },
		email: { value: '', touched: false, disabled: true }
	},
	addressHistory: {
		maxItems: 4,	// can also be passed as a prop
		items: []
	},
	nationalityList: {
		items: [ { nationality: { value: 'EN', touched: 1 } } ]
    }
};

// should personalDetails, addressHistory, nationalityList be more structured ?

const ForenameInput = () => null;
const SurnameInput = () => null;
const NationalitySection = () => null;

function zoog () {						// eslint-disable-line no-unused-vars
	const { data } = this.props;
	// must set props of child components
	return (
		<div>
            <ForenameInput {...data.forename} />
            <SurnameInput {...data.surname} />
            <NationalitySection maxItems={4} {...data.nationalityList} />
		</div>
	);
}

