import React, { Component } from 'react';

class InfoGrid extends Component {

	renderRow ( acc, def, values ) {
		if (def.render) {
			def.render(acc,def,values);
			return;
		}

		let value;
		if (def.format) {
			value = def.format(def,values);
		} else {
			value = values[def.name];
		}

		if (value==='' || value===null || value===undefined) {
			if (!def.always) {
				return;
			}
			value = '';
		}
		acc.push( <dt key={def.name+'Dt'}>{def.label || def.name}{def.tooltip}</dt> );
		acc.push( <dd key={def.name+'Dd'}>{value}</dd> );
	}

	render () {
		const { definitions, values } = this.props;

		const items = definitions.reduce( (acc,def) => {
			this.renderRow( acc, def, values );
			return acc;
		}, [] );

		return (
			<dl className="info-grid">{items}</dl>
		);
	}
}

export default InfoGrid;

/*
render a definition list
value can be in a variety of formats

const example = {
	CountryCode: 'CA',   	// use a mapper
	CurrentRate: 3.7872, 	// percentage, 2 decimal places, ie '3.79%'
	LoanAmount: 4250000,	// display as '4,250,000'
	BookingFee: {
		currency: 'USD',	// display as '379.99 USD'
		ammount: 379.99
	}
};

*/

export function optionFormatter ( def, values ) {
	const val = values[def.name];
	return def.mapper[val] || val;
}

export function percentFormatter ( def, values ) {
	const val = values[def.name];
	if (isNaN(val) || typeof val !== 'number') {
		return '';
	}
	return val.toFixed(2) + '%';
}

export function dateFormatter ( def, values ) {
	const val = values[def.name];
	if (val && val.year) {
		return val.day+'-'+val.month+'-'+val.year;
	}
	return '';
}



