import React, { Component } from 'react';

export class Announcer extends Component {

    componentDidMount () {
        textUsage = {};
    }

    render () {
        const props = {
            id: 'screenReaderAnnouncer',
            // role: 'alert',
            'aria-atomic': 'true',
            // 'aria-live': 'assertive',
            style: {
                position: 'absolute',
                left: '-10000px',
                top: 'auto',
                overflow: 'hidden'
            }
        };

        return (
            <div {...props} />
        );
    }

}

let textUsage = {};
let textToRead = [];
let nextUpdateTimer;

function announce () {
    let text = textToRead.join(', ');
    textToRead = [];
    const it = document.getElementById('screenReaderAnnouncer');
    if (!it) {
        console.log('ANNOUNCE NA ['+text+']'); // eslint-disable-line no-console
        return;
    }

    const times = textUsage[text] || 0;
    textUsage[text] = times + 1;

    for (let n=0;n<times;n++) { // make it unique
        text += '\u00A0';
    }

    // it.innerHTML = text; // this works fine on JAWS 18, but not on 16

    // JAWS 16 - reset silently
    it.setAttribute('aria-live','off');
    it.innerHTML = '';

    // switch assertive back on
    it.setAttribute('role','alert');    // REQUIRED BY APPLE VOICEOVER
    it.setAttribute('aria-live','assertive');

    // add an element to announce
    const dv = document.createElement('div');
    dv.setAttribute('id','idAlertNow'+Date.now());
    dv.innerHTML = text;
    it.appendChild(dv);

    // re-enable this to debug announcements
    console.log('ANNOUNCE ['+text+']'); // eslint-disable-line no-console
}

export function announceText (text, insert) {
    clearTimeout(nextUpdateTimer);
    if (insert) {
        textToRead.unshift(text);
    } else {
        textToRead.push(text);
    }
    nextUpdateTimer = setTimeout( () => announce(), 100 );
}

export default Announcer;



