import React from 'react';
import PropTypes from 'prop-types';

export default class FormBasis extends React.Component {

    static propTypes = {
        id: PropTypes.string
    };

    static childContextTypes = {
        getParentSection: PropTypes.func
    };

    constructor (props) {
        super(props);
        this.state = { values: {}, touched: {} };
    }

    getChildContext() {
        return {
            getParentSection: () => {
                return this
            }
        };
    }

    setFormData (values, touched) {
        this.setState( { values, touched } )
    }

    onFieldChange (id, value, touched) {
        const modValues = {...this.state.values};
        const modTouched = {...this.state.touched};
        modValues[id] = value;
        modTouched[id] = touched;
        this.setFormData(modValues,modTouched);
    }

    onFieldPropChange (id, touched) {
        const values = this.state.values;
        const modTouched = {...this.state.touched};
        modTouched[id] = touched;
        this.setFormData(values,modTouched);
    }

    /*getParent() {
        return this;
    }*/

    getSectionId() {
        return this.props.id;
    }

    getValue (id,def) {
        return this.state.values[id] || def;
    }

    getFieldProps (id,def) {
        return this.state.touched[id] || def;
    }

    isTouched (id) {
        const fpr = this.state.touched[id] || {};
        return !! fpr.touched;
    }

    sectionProps(id) {
        // console.log('IS IT AN ARRAY',id,id.isArray,typeof id);
        if (typeof id === 'object') {
            let base = {
                id: id[0],
                values: this.getValue(id[0],{}),
                touched: this.getFieldProps(id[0],{})
            };
            id.forEach( (key,index) => {
                if (index>0) {
                    base = {
                        id: key,
                        values: base.values[key] || {},
                        touched: base.touched[key] || {}
                    };
                }
            });
            return base;
        }
        return {
            id: id,
            values: this.getValue(id,{}),
            touched: this.getFieldProps(id,{})
        };
    }

}



