import React, { Component } from 'react';
import './App.css';

class SelectInput extends Component {

  render () {
	  const { form, id, labelText, options, renderedOptions, hidden = [], more } = this.props;

    const value = form.getValue(id);
    const error = !value ? 'Please select '+labelText : undefined;
    const invalid = (form.state.touched[id] && !!error) || undefined;
    const describedBy = invalid ? id+'-Error' : undefined;
    const gray = value==='' ? 'gray' : undefined;

    let blank = null;
    
    const opts = renderedOptions || options.reduce( (acc,val) => {
      const {id,text} = val;
      if (id==='') {
        if (value==='') // do not add the '' option if a selection has been made
          blank = <option key="" value="" className="gray" disabled>{text}</option>;
      } else {
        if (hidden.indexOf(id)>=0) {
          return acc;
        }
        acc.push( <option key={id} value={id} disabled={val.disabled}>{text}</option> );
      }
      return acc;
    }, []);
    
    // aria-label={'AL '+labelText} // works on Safari
    return (
      <div>
        <label htmlFor={id} className={ invalid ? 'invlabel' : undefined }>{labelText}</label>
        <select
          id={id}
          {...more}
          onBlur={form.onBlur}
          onChange={form.onEdit}
          value={value}
          aria-invalid={invalid}
          aria-describedby={describedBy}
          className={ invalid ? 'inval' : undefined || gray }
        >{blank}{opts}</select>
        {invalid && form.renderError(id,error)}
      </div>
    );
  }

}

export default SelectInput;

/*

pass in renderedOptions - pre-rendered array of <options>
this will improve performance ie with country list

*/


