import FieldComponent from './FieldComponent';
import SelectField from './SelectField'

const titles = [
    { id: '', text: 'Please select a title' },
    { id: 'Mr', text: 'Mr' },
    { id: 'Mrs', text: 'Mrs' },
    { id: 'Dr', text: 'Doctor' },
    { id: 'OTH', text: 'Other' }
];

export default class PersonTitle extends FieldComponent {

    static defaultProps = {
        id: 'personTitle',
        Component: SelectField,
        value: '',
        label: {
            id: 'perTTL', defaultMessage: 'Title'
        },
        editProps: {
            options: titles
        }
    };

    validate () {
        // console.log( 'validate PersonalTitle', this.props );
        //return PersonTitle.schemaValidate(this.props,this.getParent().getData());
    }

    static schemaValidate (props, section) {
        //console.log( 'schemaValidate PersonalTitle', props, section );
        return [];
    }
}



