import React from 'react';
import PropTypes from 'prop-types';
import Button from '../Button';
import FormBasis from './FormBasis';
import FormRepeatingSection from './FormRepeatingSection';
import PhoneSection from './PhoneSection';

export default class TestRepeatForm extends FormBasis {

    static propTypes = {
        onDebug: PropTypes.func
    };

    onAddOne = () => {
        // TODO - can only add one if all current inputs are valid !!!
        // PhoneSection.validate( values[0] ) ???
        const values  = [...this.getValue('phoneList',[])];
        const touched = [...this.getFieldProps('phoneList',[])];
        // values.push({ __specialKey: Date.now() });
        values.push( PhoneSection.getDefaultValues() );
        touched.push({});
        // this.setFormData(values,touched);
        this.onFieldChange( 'phoneList', values, touched );
    };

    onClickTest = () => {
        console.log('TEST',this.state);
        if (this.props.onDebug)
            this.props.onDebug(this.state);
    };

    render () {
        return (
            <div className="form-fields">
                <p>Here's a repeating section</p>

                <FormRepeatingSection
                    component={PhoneSection}
                    id="phoneList"
                    values={this.getValue('phoneList',[])}
                    touched={this.getFieldProps('phoneList',[])}
                />

                <div className="button-bar">
                    <Button id="idDebug" onClick={this.onClickTest} className='white'>Debug</Button>
                    <Button id="idAddOne" onClick={this.onAddOne} className='primary'>Add One</Button>
                </div>
            </div>
        );
    }
}



