import React, { Component } from 'react';

export default class SelectField extends Component {

    render () {
        const { id, value, options, renderedOptions, hidden = [] } = this.props;

        const gray = value==='' ? 'gray' : undefined;
        const invalid = false;

        let blank = null;

        const opts = renderedOptions || options.reduce( (acc,val) => {
            const {id,text} = val;
            if (id==='') {
                if (value==='' || value===undefined) // do not add the '' option if a selection has been made
                    blank = <option key="" value="" className="gray" disabled>{text}</option>;
            } else {
                if (hidden.indexOf(id)>=0) {
                    return acc;
                }
                acc.push( <option key={id} value={id} disabled={val.disabled}>{text}</option> );
            }
            return acc;
        }, []);

        return (
            <select
                id={id}
                onBlur={this.props.onBlur}
                onChange={this.props.onChange}
                value={value}
                className={ invalid ? 'inval' : gray }
            >{blank}{opts}</select>
        );
    }
}



