import React, { Component } from 'react';
import PropTypes from 'prop-types';

class FieldComponent extends Component {

    static contextTypes = {
        getParentSection: PropTypes.func
    };

    getParent () {
        return this.context.getParentSection();
    }

    onBlur = (ev) => {
        console.log('onBlur',this.props.id,ev.target.value);
        // this.getParent().onTouchField(this.props.id,true);
    };

    onChange = (ev) => {
        const elemId = this.getParent().getSectionId()+'-'+this.props.id;
        console.log('onChange',elemId,ev.target.value);
        // TODO pass this up to the controlling section
        this.getParent().onFieldChange( this.props.id, ev.target.value );
    };

    getProps () { // override if you like
    }

    render () {
        const { id, label } = this.props;
        const cprops = this.getProps() || this.props.editProps;
        // declared on the final class
        // const value = this.props.value;  // value set by form, from redux
        const sect = this.getParent();
        const value = sect.getValue(id,'');
        const touched = sect.isTouched(id);  // true if form is set to show all errors
        const elemId = this.getParent().getSectionId()+'-'+id;

        // console.log('FieldComponent render '+id,value);
        // disabled - true / false
        // show - true / false

        // const invalid = false;
        // invalid={invalid} --- not for input
        this.validate();

        return (
            <div>
                {label && <div><label htmlFor={elemId}>{label.defaultMessage}</label></div>}
            <this.props.Component
                style={{ color: touched ? 'blue' : '#000' }}
                {...cprops}
                id={elemId}
                value={value}
                onBlur={this.onBlur}
                onChange={this.onChange}
            />
            </div>
        );

    }

}

export default FieldComponent;



