import React, { Component } from 'react';
import './App.css';

class DateInput extends Component {

	getValue () {
		const { form, id } = this.props;
		return form.getValue(id,{ day: '', month: '', year: '' });
	}

	onBlur = () => {
		//this.doBlurT = setTimeout(() => {
		const { form, id } = this.props;
		form.onBlur( { target: { id: id } } );
		//},50);
	};

	onFocus = () => {
		// clearTimeout(this.doBlurT);
		this.props.form.cancelTouchError();
	};

	onEdit = (e) => {
		const { form, id } = this.props;
		// console.log('DED',e.target.id,e.target.value);
		// this.props.form.onEdit(e);
	    const value = this.getValue();
	    if (e.target.id===id) value.day = e.target.value;
	    else if (e.target.id===id+'Month') value.month = e.target.value;
	    else value.year = e.target.value;
	    form.onEdit( { target: { id: id, value: value } } );
	};

	renderInput (aria,cn,id,placeholder,value,invalid,max) {
		if (invalid) {
			cn += ' inval';
		}
	    const describedBy = invalid ? id+'-Error' : undefined;
		return (
			<input
		        aria-describedby={describedBy}
	            aria-invalid={invalid}
				aria-label={aria} 
				className={cn} 
				id={id}
				maxLength={max}
			 	onBlur={this.onBlur} 
			 	onChange={this.onEdit} 
			 	onFocus={this.onFocus} 
			 	placeholder={placeholder}
			 	type="text"
			 	value={value}
			 />
		);
	}

	render () {
		const { form, id, labelText, tooltip } = this.props;

	    const value = this.getValue();
	    
	    const year = parseInt(value.year,10);
	    const error = isNaN(year) || year<1900 ? 'Please enter a valid year for '+labelText : undefined;
	    // const error = !value ? 'Please enter '+labelText : undefined;

	    const invalid = (form.isTouched(id) && !!error) || undefined;
	    
	    //const describedBy = invalid ? id+'-Error' : undefined;
	    //const className = invalid ? 'inval' : undefined;
	    const labelClass = invalid ? 'invlabel' : undefined;

		return (
	      <div>
	      	<div>
		        <label style={{ display: 'inline-block', width: 'auto' }} htmlFor={id} className={labelClass}>{labelText}</label>
		        {tooltip}
	        </div>
	      	<div>
	      		{this.renderInput(labelText+', day','date-2',id,'DD',value.day,invalid,2)}
	      		{this.renderInput(labelText+', month','date-2',id+'Month','MM',value.month,invalid,2)}
	      		{this.renderInput(labelText+', year','date-4',id+'Year','YYYY',value.year,invalid,4)}
	      	</div>
	        {invalid && form.renderError(id,error)}
	      </div>
	    );
	}
}

export default DateInput;

/*
	        <input
	          id={id}
	          type="text"
	          onBlur={form.onBlur}
	          onChange={form.onEdit}
	          value={value}
	          aria-invalid={invalid}
	          aria-describedby={describedBy}
	          className={className}
	        />
*/



