import React from 'react';
import PropTypes from "prop-types";
import FormSection from './FormSection';

export default class FormRepeatingSection extends FormSection {

    static propTypes = {
        id: PropTypes.string,
        values: PropTypes.arrayOf(PropTypes.shape({})),
        touched: PropTypes.arrayOf(PropTypes.shape({}))
    };

    test (id,comp,array,touched) {
        return <FormRepeatingSection component={comp} id={id} touched={touched} values={array} />;
    }

    getSectionCount () {
        return this.props.values.length;
    }

    onFieldChange (id, value, touched) {
        const modValues = [...this.props.values];
        const modTouched = [...this.props.touched];
        modValues[id] = value;
        modTouched[id] = touched;
        this.getParent().onFieldChange(this.props.id, modValues, modTouched);
    }

    deleteSectionIndex (index) {
        // console.log('deleteSectionIndex',index);
        const modValues = [...this.props.values];
        const modTouched = [...this.props.touched];
        modValues.splice(index,1);
        modTouched.splice(index,1);
        this.getParent().onFieldChange(this.props.id, modValues, modTouched);
    }

    // static sillyKey = 1000;

    render () {
        //console.log('FormRepeatingSection',this.props);
        const array = this.props.values;
        //if (!array || !array.isArray() || !array.length) {
        //    return null;
        //}
        const touched = this.props.touched;
        const Comp = this.props.component;
        const rows = [];
        array.forEach( (row,index) => {
            rows.push(
                <Comp key={row.__specialKey || index} id={index} touched={touched[index]} values={row} />
            );
        });

        return (
            <div>{rows}</div>
        );
    }
}



